<template>
  <div id="home-container">
    <h1>{{ msg }}</h1>
    <div>
      {{ newWord }}
    </div>
    <div>
      {{ this.latitude }} + ' '
      {{ this.longitude }}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: 'Welcome to Vue.js App!',
      newWord: 'Something',
      geolection: 0,
      latitude: 0,
      longitude: 0
    }
  },
  methods: {
    geolocation () {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(function (position) {
          // Get the coordinates of the current position.
          var lat = position.coords.latitude
          var lng = position.coords.longitude
          // Create a new map and place a marker at the device location.
          // var map = new GMaps({
          //     el: '#map',
          //     lat: lat,
          //     lng: lng
          // });
          // map.addMarker({
          //     lat: lat,
          //     lng: lng
          // });
          this.latitude = lat
          this.longitude = lng
        })
      }
    }
  },
  mounted () {
    this.geolocation()
  }
}
</script>

<style scoped>
#home-container{
  height: 100%;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



